.projects, #background {
  height: 90vh;
}

#background {
  width: 100vw;
}

.projects h1 {
  position: absolute;
  left: 31.45%;
  top: 14.8%;
  font-weight: 900;
  font-size: 100px;
  background: linear-gradient(180deg, #000000 0%, #494949 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.projects h2 {
  position: absolute;
  font-weight: 300;
  left: 61.4%;
  top: 25.5%;
  transform: rotate(-2deg);
}

/* ALL CARDS */

.font-one,
.font-two,
.font-three,
.font-four,
.font-five,
.font-six {
  font-family: "Protest Strike", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 30px;
  text-transform: uppercase;
}

.card p {
  color: #fff;
}

#shadow-one,
